*{
font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}

input{
  font-size: 1.2rem;
  width: 100%;
  color:rgb(124, 124, 124);
}

.card-grid{
display: flex;
flex-direction: row;
flex-wrap: wrap;

}

.card{
  align-content: center;

  border:  1px solid rgb(141, 132, 132);

  border-radius: 10px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding:5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}